header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header_socials{
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: center;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background-color: var(--color-primary);
}

.me {
    /*background-color: var(--color-primary);*/
    width: 22rem;
    height: 22rem; /* Set the height to match the width for a perfect circle */
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem; /* Use 'top' instead of 'margin-top' to maintain positioning */
    border-radius: 50%; /* Make the container perfectly round */
    overflow: hidden;
    /*padding: 1rem 1.5rem 1.5rem 1.5rem*/
}

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

@media screen and (max-width:1024px) {
    header{
        height: 68vh;
    }
}

@media screen and (max-width:600px) {
    header{
        height: 100vh;
        padding-top: 4rem;
    }
    .header_socials, .scroll_down{
        display: none;
    }
    .me{
        margin-top: 0;
    }
}